// my-app\src\components\Block\index.tsx
import { withTranslation } from "react-i18next";
import { Container, TextWrapper, Title, Content } from "./styles";

interface Props {
  title: string;
  content: string;
  glowing: boolean;
  t: any;
}

const Block = ({ title, content, glowing, t }: Props) => {
  return (
    <Container>
      <Title glowing={glowing}>{t(title)}</Title>
      <TextWrapper>
        <Content>{t(content)}</Content>
      </TextWrapper>
    </Container>
  );
};

export default withTranslation()(Block);
