import { useState, useEffect } from "react";
import { notification } from "antd";
import axios from "axios";
import { validateProps } from "../../common/types";

type ValidateFunction = (values: validateProps) => validateProps;

interface UseFormReturn {
  handleChange: (event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => void;
  handleSubmit: (event: React.ChangeEvent<HTMLFormElement>) => void;
  values: validateProps;
  errors: validateProps;
  setValues: React.Dispatch<React.SetStateAction<validateProps>>;
  setErrors: React.Dispatch<React.SetStateAction<validateProps>>;
  toggleInputValues: { [key: string]: string };  // Add this line
  setToggleInputValues: React.Dispatch<React.SetStateAction<{ [key: string]: string }>>; 
}

export const useForm = (validate: ValidateFunction): UseFormReturn => {
  const [values, setValues] = useState({
    name: '',
    message: '',
    contactInfo: ''
  });
  const [errors, setErrors] = useState<validateProps>({
    name: '',
    message: '',
    contactInfo: ''
  });
  const [shouldSubmit, setShouldSubmit] = useState(false);

  const openNotificationWithIcon = () => {
    notification["success"]({
      message: "Success",
      description: "Your message has been sent!",
    });
  };


  const handleSubmit = (event: React.ChangeEvent<HTMLFormElement>) => {
    event.preventDefault();
    
    // Validate the contactInfo field
    if (!values.contactInfo) {
      setErrors((errors: validateProps) => ({ ...errors, contactInfo: "At least one contact method is required" }));
    } else {
      // If contactInfo is valid, validate the other fields
      setErrors(validate(values));
    }
  
    // Check if all fields have values
    if (values.name && values.contactInfo && values.message) {
      // Your url for API
      const url = "";  // just a placeholder, you might want to replace or remove this
      axios.post('https://droplet.marsbirds.art/form-submit', values)
        .then(() => {
          setShouldSubmit(true);
        })
        .catch(error => {
          if (error.response) {
            console.error("Server responded with an error status:", error.response.status);
            console.error("Error data:", error.response.data);
          } else if (error.request) {
            console.error("No response received from the server:", error.request);
          } else {
            console.error("Error setting up the request:", error.message);
          }
        });
    }
  };

  const [toggleInputValues, setToggleInputValues] = useState<{ [key: string]: string }>({
    Email: "",
    Discord: "",
    Twitter: "",
    Phone: "",
    Other: ""
  });

  useEffect(() => {
    if (Object.keys(errors).length === 0 && shouldSubmit) {
      setValues({
        name: '',
        message: '',
        contactInfo: ''
      });
      setToggleInputValues({
        Email: "",
        Discord: "",
        Twitter: "",
        Phone: "",
        Other: ""
      });
      openNotificationWithIcon();
    }
  }, [errors, shouldSubmit, setToggleInputValues]);

  const handleChange = (
    event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    event.persist();
    const { name, value } = event.target;
    setValues((values) => ({
      ...values,
      [name]: value,
    }));
    
    // Trigger validation for the changed input
    const tempErrors = validate({ ...values, [name]: value });
    setErrors((errors: validateProps) => ({
      ...errors,
      [name]: tempErrors[name] || ""  // Clear error if valid or set the new error
    }));
  };

  return {
    handleChange,
    handleSubmit,
    values,
    errors,
    setValues,
    setErrors,
    toggleInputValues,  // add this line if toggleInputValues is meant to be used outside of useForm
    setToggleInputValues 
  };
};
