import styled, { keyframes, css } from "styled-components";

interface FormGroupProps {
  glowing: boolean;
}

interface ContactContainerProps {
  glowing: boolean;
}

interface TitleProps {
  glowing: boolean;
}

// Define the keyframe animations for the neon glow
const neonGlow = keyframes`
  0% {
    text-shadow: 
      0 0 4px #98b9fd,
      0 0 10px #286fff, 
      0 0 26px #286fff;
  }
  100% {
    text-shadow: 
      0 0 6px #98b9fd,
      0 0 12px #286fff, 
      0 0 24px #286fff;
  }
`;

export const ContactContainer = styled("div")<ContactContainerProps>`
  padding: 1rem;
  border: ${(props) => (props.glowing ? '2px solid #286fff' : 'none')};
  box-shadow: ${(props) => (props.glowing ? '0 0 10px #286fff, 0 0 20px #286fff' : 'none')};
  transition: box-shadow 0.3s ease-in-out, border 0.3s ease-in-out;

  @media only screen and (max-width: 768px) {
    padding: 2rem;
    border: 2px solid #286fff;
    box-shadow: 0 0 10px #286fff, 0 0 20px #286fff;
    width: 100%;
  }
`;

const Label = styled.label`
    display: block;
    margin-bottom: 0.5rem;
    font-weight: bold;
`;

export const FormGroup = styled.form<FormGroupProps>`
  width: 80%;
  max-width: 520px;
  margin: auto;
  border: ${(props) => (props.glowing ? '2px solid #286fff' : 'none')};
  box-shadow: ${(props) => (props.glowing ? '0 0 10px #286fff, 0 0 20px #286fff' : 'none')};
  transition: box-shadow 0.3s ease-in-out, border 0.3s ease-in-out;

  @media only screen and (max-width: 1045px) {
    margin-top: 0rem;
    margin-right: auto;
    margin-left: auto;
    width: 100%;
  }
  
  @media only screen and (max-width: 768px) {
    width: 100%;
  }
`;

export const Span = styled("span")<any>`
  display: block;
  font-weight: 600;
  border: ${(props) => (props.glowing ? '2px solid #286fff' : 'none')};
  box-shadow: ${(props) => (props.glowing ? '0 0 10px #286fff, 0 0 20px #286fff' : 'none')};
  height: 0.5rem;
  padding: 0 0.675rem;
`;

export const ButtonContainer = styled("div")`
  text-align: end;
  position: relative;

  @media only screen and (max-width: 414px) {
    text-align: center;
    padding-top: 0.5rem;
  }
`;

export const Title = styled("h6")<TitleProps>`
  font-size: 2.9em;
  line-height: 1em;
  font-weight: bold;
  height: auto;
  width: auto;
  padding-top: 0rem;
  text-align: center;
  color: #ffffff;
  
  ${(props) => props.glowing && css`
    color: #286fff;
    animation: ${neonGlow} 1.5s ease-in-out infinite alternate;
  `}

  @media only screen and (max-width: 768px) {
    text-align: center;
    margin-top: calc(-1rem - 1.5vw);
    margin-bottom: 3rem;
    color: #286fff;
    text-shadow: 
      0 0 4px #fff, 
      0 0 10px #fff,
      0 0 20px #286fff, 
      0 0 30px #286fff, 
      0 0 40px #286fff, 
      0 0 50px #286fff, 
      0 0 60px #286fff;
  }
`;

export const Content = styled("p")`
  font-size: clamp(1rem, calc(1rem + 0.5vw), 1.5rem);
  line-height: 1.4em;
  color: #2c2c2c;
  height: 20em; 
  text-align: left;
  
  @media only screen and (max-width: 768px) {
    text-align: center;
    margin-bottom: calc(1rem + 1vw);
  }
`;

export const Container = styled("div")`
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin: 0 auto;
  max-width: 700px;
  
  @media only screen and (max-width: 768px) {
    padding: 0 .2rem;
  }
`;

export const TextWrapper = styled("div")`
  border-radius: 3rem;
  max-width: 600px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

export const ImageContainer = styled("div")`
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 0 1rem;
`;
