import { StyledButton } from "./styles";
import { ButtonProps } from "../types";

export const Button = ({
  color,
  children,
  onClick,
  glowing,
  fixedWidth,
}: ButtonProps) => (
  <StyledButton color={color} onClick={onClick} glowing={glowing} fixedWidth={fixedWidth || false}>
    {children}
  </StyledButton>
);